.banner-container {
    position: relative;
    display: flex;
    width: 100%;
    justify-content: center;
    padding-top: var(--mobile-padding);
}

.text {
    color: var(--text-primary-color);
    font-size: var(--desktop-font-size);
    text-align: center;
    height: 100%;

}

#astronaut {
    animation: move-up-and-down 3.5s normal infinite;
    position: relative;
    width: var(--desktop-section-image-width);
}

@media (max-width: 55em) {
    .banner-container {
        padding-top: var(--mobile-pad-top);
        flex-direction: column-reverse;
        align-items: center;
        gap:var(--mobile-gap);
        padding-top: var(--mobile-padding);
    }
    .text {
        font-size: var(--mobile-font-size)
    }
    #astronaut {
        position: relative;
        width: var(--mobile-section-image-width);
        height: auto;
    }
  }

  @keyframes move-up-and-down {
    0%, 100% {
    transform: translateY(1em);
    }
    50% {
    transform: translateY(-1em);
    }
  }