.navbar-container {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 1em;
}

.logo {
  position: relative;
  width: 7em;
  height: auto;
}

.buttons-container {
  display: flex;
  gap: 0.8em;
}

.button-constant {
  margin-top: 0.60em;
  padding: 0.2em 0.5em;
}

a {
  all: unset;
}


@media (max-width: 40em) {

  .logo {
    width: 4em;
  }

  .buttons-container {
    gap: 0.5em;
  }

}


