.contact-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: var(--desktop-padding);
    padding-bottom: var(--desktop-padding);
    color: var(--text-primary-color);
    word-wrap: break-word;
    text-align: center;
}

.form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding: 1em;
    width: 55%;
    border-radius: 2em;
    border: 1px solid var(--text-primary-color);
    background-color: rgb(var(--primary-color));
}

.input-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 1em;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
}

.input {
    text-align: center;
    font-size: var(--desktop-font-size);
    color: var(--text-primary-color);
    border: 2px solid black;
    border-radius: 5em;
    width: 70%;
    height: 3.5rem;
    background-color: rgb(var(--third-color));
}

.input:focus {
    outline: 2px solid var(--text-primary-color);
}

#comment {
    border-radius: 0;
    height: 5em;
    text-align: start;
    font-size: 1em;
}

@media (max-width: 55em) {
    .contact-container{
        padding-top: var(--mobile-padding);
        padding-bottom: var(--mobile-padding);
        flex-direction: column;
    }

    .form {
        width: 95%;
    }

    .input {
        height: 3rem;
        width: 90%;
        font-size: var(--mobile-font-size);
    }
}

/* Handle big screens */
@media (min-width: 1950px) {
    .input {
        height: 5rem;
    }
}