.about-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: var(--desktop-padding);
}

.bullet-point-container {
    border-radius: 2em;
    padding: 1em;
    border: 1px solid var(--text-primary-color);
    background-color: rgba(var(--primary-color), 0.8);
}

.skills-container {
    width: 100%;
    padding-top: var(--desktop-padding);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: var(--desktop-gap);
}

.programming-languages-container {
    padding-top: 0.2em;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    font-size: 0.8em;
}

.programming-language-item {
    padding: 0.2em;
    border-radius: 5em;
    border: 0.5px solid var(--text-primary-color);
}

.projects-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 1rem;
    gap: 1rem;
    padding-top: 2rem;
}

.project-item-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
}

.project-image {
    cursor: pointer;
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
    border: 3px solid var(--text-primary-color);
    background-size: contain;
    background-color: rgba(var(--primary-color), 0.8);
    background-repeat: no-repeat;
    background-size: 6rem;
    background-position: center;
}

@media (max-width: 65em) {

    .about-container{
        flex-direction: column;
        align-items: center;
        padding-top: var(--mobile-padding);
    }

    .skills-container {
        display: flex;
        flex-direction: column;
        padding-top: 0;
        padding-top: var(--mobile-padding);
        gap: var(--mobile-padding);
    }

    .skills-container img{
        width: var(--mobile-section-image-width)
    }

    .programming-languages-container {
        flex-direction: column;
        font-size: var(--mobile-font-size);
    }

    .project-item-container {
        gap: 0.5rem;
    }
}