@font-face {
  font-family: 'MainFontFamily';
  src: local('MainFontFamily'), url(./assets/Poppins-Regular.ttf) format('truetype');
}

html {
  background: url(./assets/background/background.jpg) no-repeat;
  background-size: auto;
}

:root {
  --primary-color: 21, 0, 80;
  --secondary-color: 63, 0, 113;
  --third-color: 251, 37, 118;

  --text-primary-color: #fff;
  --text-secondary-color: #000000;


  --desktop-font-size: 1.5rem;
  --mobile-font-size: 1.2rem;

  --desktop-gap: 5em;
  --mobile-gap: 3em;
  --desktop-padding: 8em;
  --mobile-padding: 3em;

  --desktop-section-image-width: 27em;
  --mobile-section-image-width: 20em;

  --text-color-code-one: #fff;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: MainFontFamily;

  -webkit-user-drag: none;
  
}

.container-main {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0;
}

/* Handle big screens */
@media (min-width: 1950px) {

  html {
    background-size: 100%;
  }

  :root{
    --desktop-font-size:2.4rem;
  }
}