.footer-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 0.5em;
    gap: 0.2em;
    background-color: rgba(var(--primary-color));
    color: var(--text-primary-color);

}

.footer-container img {
    width: 2em;
}

/* Handle big screens */
@media (min-width: 1950px) {
    .footer-container {
        font-size: 1.4rem;
    }
}