.modal-background {
    position: fixed;
    background-color: rgba(0,0,0,0.8);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-container {
    border: 1px solid var(--text-primary-color);
    max-width: 55rem;
    word-wrap:break-word;
    border-radius: 5rem;
    background-color: rgba(var(--primary-color), 0.6);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: var(--text-primary-color);
    padding: 3rem;
    gap: 1rem;
    text-align: center;
    font-size: 1.4rem;
}

@media (max-width: 65em) {
    .modal-container {
        height: 80%;
        max-width: 95%;
        font-size: 1.15rem;
    }
}